/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  margin: 0;
  padding: 0;
}

.App {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 600px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  line-height: 1.3;
}

.App p, .App h2 {
  margin-bottom: 15px;
}

.App p {
  line-height: 1.5;
}

.App img {
  max-width: 100%;
}

.App h1, .App h2 {
  font-family: 'Open Sans Condensed', sans-serif;
  font-weight: 300;
  line-height: 1;
  text-transform: uppercase;
}

.App h1 {
  font-size: 48px;
  margin-top: 30px;
  margin-bottom: 30px;
  text-align: center;
}

.App h2 {
  font-size: 36px;
}

.App a {
  color: rgba(0, 144, 64, 0.8);
  font-weight: 700;
  text-decoration: none;
  -webkit-transition: color 0.3s ease-in-out;
  transition: color 0.3s ease-in-out;
}

.App a:hover {
  color: rgba(0, 144, 64, 1);
}

.App a:visited {
  color: rgba(0, 144, 64, 1);
}

.App footer p {
  font-size: 13px;
  text-align: center;
}

.App footer p i {
  color: red;
}

.App *:focus {
  outline: 1px solid rgb(0, 144, 64);
}

.App *::-moz-selection {
  background: rgba(0, 144, 64, 0.3);
}

.App *::selection {
  background: rgba(0, 144, 64, 0.3);
}

.App .update {
  font-size: 13px;
  font-weight: 700;
}

.Search {
  margin-bottom: 30px;
}

.Search label span {
  visibility: hidden;
}

.Search input {
  width: 100%;
  padding: 10px 15px;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
}
.Group {
  margin-bottom: 30px;
}
.Members {
  
}
.Member {
  display: block;
  margin-top: 5px;
  padding-left: 5px;
  padding-right: 5px;
  min-height: 100px;
}

@media all and (min-width: 500px) {
  .Member {
    display: inline-block;
    vertical-align: top;
    width: 50%;
  }
}

.Member .member-image-container {
  margin-bottom: 0;
}


.Member .member-image-container .member-image {
  max-width: 75px;
  border-radius: 100%;
  float: left;
  margin-right: 5px;
}

.Member .member-information-container {
  padding-left: 10px;
  padding-right: 10px;
  margin-left: 80px;
  font-size: 14px;
}

.Member.active .member-information-container {
  top: 100%;
  visibility: visible;
  opacity: 1;
  width: 350%;
}

.Member .member-information-container .member-name-english {
  font-weight: 700;
  text-transform: uppercase;
}

.Member .member-information-container .member-name-japanese {
  display: block;
  margin-bottom: 8px;
}

.Icon {
  display: inline-block;
  vertical-align: top;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 5px;
}

.Icon a span {
  display: none;
}

.Icon a i {
  font-weight: 700;
}
